const OrdersList = ({ orders, onCancelOrder }) => {
  return (
    <div className="mt-8">
      <h2 className="text-xl font-semibold mb-4">Existing Orders</h2>
      {orders.length === 0 ? (
        <p>No orders yet</p>
      ) : (
        <div className="space-y-4">
          {orders.map((order) => (
            <div key={order.id} className="border rounded-lg p-4 shadow-md">
              <p><strong>Order Number:</strong> {order.order_number}</p>
              <p><strong>Status:</strong> {order.status}</p>
              <p><strong>Total:</strong> ${order.total_amount}</p>
              <ul className="mt-2">
                {order.items.map((item, index) => (
                  <li key={index}>
                    Product {item.product_id} - Qty: {item.quantity} - ${item.unit_price} each
                  </li>
                ))}
              </ul>
              {order.status !== 'cancelled' && (
                <button
                  onClick={() => onCancelOrder(order.id)}
                  className="mt-2 bg-red-500 text-white px-4 py-2 rounded hover:bg-red-600"
                >
                  Cancel Order
                </button>
              )}
            </div>
          ))}
        </div>
      )}
    </div>
  );
};

export default OrdersList;