'use client';

import { useState, useEffect } from 'react';
import ProductList from '../components/ProductList';
import OrderSummary from '../components/OrderSummary';
import OrdersList from '../components/OrdersList';

export default function Home() {
  const [products, setProducts] = useState([]);
  const [loadingProducts, setLoadingProducts] = useState(true);
  const [orderItems, setOrderItems] = useState([]);
  const [orders, setOrders] = useState([]);

  useEffect(() => {
    fetchProducts();
    fetchOrders();
  }, []);

  const fetchProducts = async () => {
    try {
      const response = await fetch('http://oms-backend.test/api/products');
      if (response.ok) {
        const data = await response.json();
        setProducts(data);
      } else {
        console.error('Failed to fetch products');
      }
    } catch (error) {
      console.error('Error fetching products:', error);
    } finally {
      setLoadingProducts(false);
    }
  };

  const fetchOrders = async () => {
    try {
      const response = await fetch('http://oms-backend.test/api/orders');
      if (response.ok) {
        const data = await response.json();
        setOrders(data);
      }
    } catch (error) {
      console.error('Error fetching orders:', error);
    }
  };

  const handleAddToOrder = (product, quantity) => {
    const newItem = {
      product_id: product.id,
      unit_price: product.price,
      quantity: quantity,
    };
    setOrderItems([...orderItems, newItem]);
  };

  const handleRemoveItem = (index) => {
    const updatedItems = orderItems.filter((_, i) => i !== index);
    setOrderItems(updatedItems);
  };

  const handleConfirmOrder = async () => {
    if (orderItems.length === 0) return;

    const totalAmount = orderItems.reduce((sum, item) => sum + item.unit_price * item.quantity, 0);
    const orderPayload = {
      total_amount: totalAmount,
      status: 'created',
      items: orderItems,
    };

    try {
      const response = await fetch('http://oms-backend.test/api/orders', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(orderPayload),
      });

        console.log(orderPayload);

      if (response.ok) {
        alert('Order created successfully!');
        setOrderItems([]);
        fetchOrders(); // Refresh orders list
        fetchProducts(); // Refresh products to update inventory
      } else {
        alert('Failed to create order');
      }
    } catch (error) {
      console.error('Error creating order:', error);
      alert('Error creating order');
    }
  };

  const handleCancelOrder = async (orderId) => {
    try {
      const response = await fetch(`http://oms-backend.test/api/orders/${orderId}/cancel`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ status: 'cancelled' }),
      });

      if (response.ok) {
        alert('Order cancelled successfully!');
        fetchOrders(); // Refresh orders list
        fetchProducts(); // Refresh products to update inventory
      } else {
        alert('Failed to cancel order');
      }
    } catch (error) {
      console.error('Error cancelling order:', error);
      alert('Error cancelling order');
    }
  };

  const handleCancelCurrentOrder = () => {
    setOrderItems([]);
  };

  return (
    <div className="container mx-auto px-4 py-8">
      <h1 className="text-3xl font-bold mb-8">Order Management System</h1>
      
      <h2 className="text-2xl font-semibold mb-4">Products</h2>
      {loadingProducts ? (
        <p>Loading products...</p>
      ) : (
        <ProductList products={products} onAddToOrder={handleAddToOrder} />
      )}
      
      <OrderSummary 
        orderItems={orderItems} 
        onRemoveItem={handleRemoveItem} 
        onCancelOrder={handleCancelCurrentOrder} 
        onConfirmOrder={handleConfirmOrder} 
      />
      
      <OrdersList orders={orders} onCancelOrder={handleCancelOrder} />
    </div>
  );
}