const OrderSummary = ({ orderItems, onRemoveItem, onCancelOrder, onConfirmOrder }) => {
  const totalAmount = orderItems.reduce((sum, item) => sum + item.unit_price * item.quantity, 0);

  return (
    <div className="mt-8 border rounded-lg p-4 shadow-md">
      <h2 className="text-xl font-semibold mb-4">Order Summary</h2>
      {orderItems.length === 0 ? (
        <p>No items in order</p>
      ) : (
        <>
          <ul className="space-y-2">
            {orderItems.map((item, index) => (
              <li key={index} className="flex justify-between items-center">
                <span>
                  Product {item.product_id} - Qty: {item.quantity} - ${item.unit_price} each
                </span>
                <button
                  onClick={() => onRemoveItem(index)}
                  className="text-red-500 hover:text-red-700"
                >
                  Remove
                </button>
              </li>
            ))}
          </ul>
          <p className="mt-4 font-semibold">Total: ${totalAmount}</p>
          <div className="mt-4 space-x-2">
            <button
              onClick={onConfirmOrder}
              className="bg-green-500 text-white px-4 py-2 rounded hover:bg-green-600"
            >
              Confirm Order
            </button>
            <button
              onClick={onCancelOrder}
              className="bg-red-500 text-white px-4 py-2 rounded hover:bg-red-600"
            >
              Cancel Order
            </button>
          </div>
        </>
      )}
    </div>
  );
};

export default OrderSummary;