'use client';

import { useState } from 'react';

const ProductList = ({ products, onAddToOrder }) => {
  const [quantities, setQuantities] = useState({});

  const handleQuantityChange = (productId, quantity) => {
    setQuantities({ ...quantities, [productId]: parseInt(quantity) || 0 });
  };

  const handleAddToOrder = (product) => {
    const quantity = quantities[product.id] || 1;
    if (quantity > 0 && quantity <= product.inventory) {
      onAddToOrder(product, quantity);
      setQuantities({ ...quantities, [product.id]: 0 });
    }
  };

  return (
    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
      {products.map((product) => (
        <div key={product.id} className="border rounded-lg p-4 shadow-md">
          <h3 className="text-lg font-semibold">{product.name}</h3>
          <p className="text-gray-600">Price: PHP {product.price}</p>
          <p className="text-gray-600">Quantity: {product.stock_quantity}</p>
          <div className="mt-2">
            <input
              type="number"
              min="1"
              max={product.inventory}
              value={quantities[product.id] || ''}
              onChange={(e) => handleQuantityChange(product.id, e.target.value)}
              className="border rounded px-2 py-1 w-20 mr-2"
              placeholder="Qty"
            />
            <button
              onClick={() => handleAddToOrder(product)}
              className="bg-blue-500 text-white px-4 py-1 rounded hover:bg-blue-600"
              disabled={!quantities[product.id] || quantities[product.id] <= 0}
            >
              Add to Order
            </button>
          </div>
        </div>
      ))}
    </div>
  );
};

export default ProductList;